<template>
<div>
	<div :style='{"width":"1200px","padding":"20px","margin":"10px auto","background":"linear-gradient(90deg, rgba(168,69,67,0.2) 0%, #6D3737 55%, rgba(247,196,195,0.2) 100%)"}' class="breadcrumb-preview">
		<el-breadcrumb :separator="'/'" :style='{"fontSize":"14px","lineHeight":"14px"}'>
			<el-breadcrumb-item>Home</el-breadcrumb-item>
			<el-breadcrumb-item v-for="(item, index) in breadcrumbItem" :key="index">{{item.name}}</el-breadcrumb-item>
		</el-breadcrumb>
	</div>
	
	<div class="detail-preview" :style='{"width":"1200px","margin":"10px auto","position":"relative"}'>
		<div class="attr" :style='{"padding":"20px","position":"relative","background":"#fff","display":"flex"}'>
			<el-carousel :style='{"border":"1px solid #707070","width":"346px","margin":"0 10px 0 0","borderRadius":"219px","height":"650px"}' trigger="click" indicator-position="inside" arrow="always" type="default" direction="horizontal" height="400px" autoplay="true" interval="3000" loop="true">
				<el-carousel-item :style='{"borderRadius":"10px","width":"346px","height":"648px"}' v-for="item in tupian.split(',')" :key="item.id">
					<el-image :style='{"objectFit":"cover","width":"100%","height":"100%"}' v-if="item.substr(0,7)=='http://'" :src="item" fit="cover" class="image"></el-image>
					<el-image :style='{"objectFit":"cover","width":"100%","height":"100%"}' v-else :src="baseUrl + '/' + item" fit="cover" class="image"></el-image>
				</el-carousel-item>
			</el-carousel>
	
			
			<div class="info" :style='{"width":"640px","padding":"10px","margin":"0 0 0 10px","background":"rgba(245, 245, 245, 1)"}'>
				<div class="item" :style='{"padding":"10px 20px","margin":"0 0 10px 0","alignItems":"center","background":"#f5f5f5","justifyContent":"space-between","display":"flex"}'>
					<div :style='{"color":"rgba(0, 0, 0, 1)","fontSize":"16px"}'>{{title}}</div>
				</div>

				<div class="item" :style='{"padding":"10px","margin":"0 0 10px 0","background":"#f5f5f5","justifyContent":"spaceBetween","display":"flex"}'>
					<div class="lable" :style='{"padding":"0 10px","color":"#000","textAlign":"right","width":"102px","fontSize":"14px","lineHeight":"40px","height":"40px"}'>Book Name</div>
					<div  :style='{"padding":"0 10px","color":"#666","background":"rgba(255, 255, 255, 1)","width":"480px","fontSize":"14px","lineHeight":"40px","height":"40px"}'>{{detail.tushumingcheng}}</div>
				</div>
				<div class="item" :style='{"padding":"10px","margin":"0 0 10px 0","background":"#f5f5f5","justifyContent":"spaceBetween","display":"flex"}'>
					<div class="lable" :style='{"padding":"0 10px","color":"#000","textAlign":"right","width":"102px","fontSize":"14px","lineHeight":"40px","height":"40px"}'>Seller Number</div>
