				<div v-html="detail.neirongxinxi"></div>
			</el-tab-pane>
		</el-tabs>
		<el-dialog title="Examined" :visible.sync="sfshVisiable" width="50%">
			<el-form ref="shForm" :model="shForm" label-width="80px">
				<el-form-item label="Audit status">
					<el-select v-model="shForm.sfsh" placeholder="Audit status">
						<el-option label="Pass" value="Yes"></el-option>
						<el-option label="Fail" value="No"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="content">
					<el-input type="textarea" :rows="8" v-model="shForm.shhf"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="sfshVisiable = false">Cancel</el-button>
				<el-button type="primary" @click="shHandler">O K</el-button>
			</div>
		</el-dialog>
