</template>

<script>
  import CountDown from '@/components/CountDown';
  export default {
    //数据集合
    data() {
      return {
        tablename: 'liaotianjilu',
        baseUrl: '',
        breadcrumbItem: [
          {
            name: 'Details info'
          }
        ],
        title: '',
        tupian: '',
        endTime: 0,
        detail: {},
        activeName: 'first',
        total: 1,
        pageSize: 5,
        totalPage: 1,
        rules: {
          content: [
            { required: true, message: 'Please enter the content', trigger: 'blur' }
          ]
        },
        buynumber: 1,
        sfshVisiable: false,
        shForm: {
          sfsh: 'Yes',
          shhf: ''
        },
      }
    },
    created() {
      this.baseUrl = this.$config.baseUrl;
      this.detail = JSON.parse(this.$route.query.detailObj);
      if(this.$route.query.orderDiscuss == 'Yes') this.detail.id = this.detail.goodid;
      this.$http.get(this.tablename + '/detail/'  + this.detail.id, {}).then(res => {
        if (res.data.code == 0) {
          this.detail = res.data.data;
        }
      });
      if(this.detail.refid > 0) {
        this.$http.get(this.detail.tablename + '/detail/'  + this.detail.refid, {}).then(res => {
          if (res.data.code == 0) {
            this.detail = res.data.data;
          }
        });
      } else {
      }
      if(this.detail.goodid > 0) {
        this.$http.get(this.detail.tablename + '/detail/'  + this.detail.goodid, {}).then(res => {
          if (res.data.code == 0) {
            this.detail = res.data.data;
          }
        });
      } 

      this.startTime = new Date().getTime();
      this.endTime = new Date(this.detail.reversetime).getTime();

    },
    //方法集合
    methods: {
      onSH() {
        this.sfshVisiable = true;
      },
      shHandler(){
        this.$confirm(`Determine operation?`, "Tips", {
          confirmButtonText: "Confirm",
          cancelButtonText: "Cancel",
          type: "warning"
        }).then(() => {
          this.detail.sfsh = this.shForm.sfsh;
          this.detail.shhf = this.shForm.shhf;
          this.$http.post(`${this.tablename}/update`, this.detail).then(res => {
            if (res.data.code == 0) {
              this.sfshVisiable = false;
              this.$message({
                message: "Operation succeeded",
                type: "success",
                duration: 1500,
              });
            }
          });
        });
      },
      curChange(page) {
        this.getInfo(page);
      },
      prevClick(page) {
        this.getInfo(page);
      },
      nextClick(page) {
        this.getInfo(page);
      },


    },
    components: {
      CountDown
    }
  }
</script>

